.rt-swiper-slider {
  overflow: hidden;
}
.rt-swiper-slider .swiper-wrapper {
  align-items: center;
}

.hover-visibility .swiper-navigation .swiper-button {
  opacity: 0;
  visibility: hidden;
}
.hover-visibility:hover .swiper-navigation .swiper-button {
  opacity: 1;
  visibility: visible;
}
.hover-visibility:hover .swiper-navigation .swiper-button-prev {
  left: 12px !important;
}
.hover-visibility:hover .swiper-navigation .swiper-button-next {
  right: 12px !important;
}

.swiper-navigation .swiper-button-prev {
  left: 12px;
}
.swiper-navigation .swiper-button-prev i {
  transform: rotate(180deg);
}
.swiper-navigation .swiper-button-next {
  right: 12px;
}
.swiper-navigation .swiper-button {
  position: absolute;
  top: 50%;
  width: 50px;
  height: 50px;
  margin-top: -22px;
  z-index: 2;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 6px;
  background-color: #FFFFFF;
  transition: all 520ms ease;
  color: var(--rt-title-color);
}
.swiper-navigation .swiper-button:hover {
  background-color: var(--rt-secondary-color);
  color: var(--rt-title-color);
}
.swiper-navigation .swiper-button:after {
  display: none;
}

.swiper-pagination {
  position: relative;
  margin-top: 50px;
  display: flex;
  justify-content: center;
  bottom: 0 !important;
  z-index: 1;
}
.swiper-pagination .swiper-pagination-bullet {
  width: 20px;
  height: 20px;
  border-radius: 100%;
  transition: all 0.21s ease-in-out;
