  text-align: center;
  cursor: pointer;
  z-index: 1;
}
.swiper-scrollbar .swiper-scrollbar-drag i {
  font-size: 12px;
}

.swiper {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  z-index: 1;
}

.swiper-3d,
.swiper-3d.swiper-css-mode .swiper-wrapper {
